import * as React from 'react';
import { useState } from 'react';
import * as CSS from 'csstype';

const Leadely : React.FC = () => {
    const [codeside, resizeCodeside] = useState(false);
    const [infoside, resizeInfoside] = useState(false);

    const toggleCodeside = () => {
        if (infoside) {
            resizeInfoside(false);
            resizeCodeside(!codeside);
        } else {
            resizeCodeside(!codeside)
        }
    }

    const toggleInfoside = () => {
        if (codeside) {
            resizeCodeside(false);
            resizeInfoside(!infoside);
        } else {
            resizeInfoside(!infoside);
        }
    }

    const fadeout : CSS.Properties = {
        opacity: 0,
        transition: "1.5s",
        position: "absolute",
        pointerEvents: "none",
    }

    const fadein : CSS.Properties = {
        opacity: 1,
        transition: "1s"
    }

    const background : CSS.Properties = {
        color: infoside ? "#23272B" : "gray",
        fontSize:"2.3vh",
        height:"100vh",
        background:"no-repeat url(/img/leadelyPC.png)",
        backgroundSize: "cover",
        backgroundColor: codeside ? "#b5b5b5" : "#e3e3e3",
        backgroundBlendMode: codeside ? "darken" : infoside ? "lighten" : "normal"
    }

    return (
        <div className="d-flex w-100 align-items-center overflow-hidden" style={background}>
            <div className={`${codeside ? "w-75" : infoside ? "w-25" : "w-50"} h-100 d-flex flex-column align-items-center justify-content-center px-4 border-end`}
                 onClick={() => toggleCodeside()}
                 style={{ transition:"1s"}}>
                    <h5 style={codeside ? fadeout : fadein}>
                        <span style={{textDecoration: "none", color:"#000", fontSize:"3vh"}}>Github not available</span>
                    </h5>

                    <div style={codeside || infoside ? fadeout : fadein}>
                        <span>Personal planning application based on React-Redux</span>
                    </div>

                    <div className="d-flex flex-column align-items-center overflow-hidden" style={!codeside ? fadeout : fadein }>
                        <div className="d-flex align-items-center flex-column">
                            <div style={{color:"#000", fontSize:"3vh", borderBottom: "3px solid #000"}}>
                                <span>Frontend:</span>
                            </div>
                            <ul className="d-flex my-4 align-items-baseline flex-wrap" style={{listStyle:"none"}}>
                                <li className="px-2"><img style={{width:"10vh", height:"10vh"}} src="/img/html-icon.svg" alt="HTML" /></li>
                                <li className="px-2"><img style={{width:"10vh", height:"10vh"}} src="/img/css-icon.svg" alt="CSS" /></li>
                                <li className="px-2"><img style={{width:"12vh", height:"12vh"}} src="/img/react-icon.svg" alt="ReactJS" /></li>
                                <li className="px-2"><img style={{width:"10vh", height:"10vh"}} src="/img/javascript-icon.svg" alt="Javascript" /></li>
                                <li className="px-2"><img style={{width:"10vh", height:"10vh"}} src="/img/flow.png" alt="Flow" /></li>
                            </ul>
                        </div>
                    </div>
                 </div>

            <div className={`${infoside ? "w-75" : codeside ? "w-25" : "w-50"} h-100 d-flex flex-column align-items-center justify-content-center px-4`}
                 onClick={() => toggleInfoside()}
                 style={{ transition:"1s" }}>
                    <a style={{textDecoration: "none", color:"#000", fontSize:"3vh"}} href="https://leadely.com">Leadely</a>
                     <div style={codeside || infoside ? fadeout : fadein}>
                        <span>Small startup company</span>
                     </div>

                     <div className="d-flex flex-column align-items-center" style={ infoside ? fadein : fadeout}>
                         <span>I've been working aroun half-year as frontend developer at Leadely.com</span>
                         <ul className="d-flex flex-column align-items-baseline my-2" style={{listStyle:"none"}}>
                             <li className="my-1 w-100">First what I've done - cover almost all frontend(~95%) by tests from 0. And with new functional I continue do it.</li>
                             <li className="my-1 w-100">Design and developed Impression functional. By this you can add and track your impression of day. Like happy or angry, etc.</li>
                             <li className="my-1 w-100">Design and developed Notes functional. With this feature you can do a simple dairy or just mark something important.</li>
                             <li className="my-1 w-100">Design and developed Sprints functional. Long-period planning feature.</li>
                             <li className="my-1 w-100">Design and developed Tags functional. Validate your tasks as Important/Urgent/Maybe tags.</li>
                             <li className="my-1 w-100">Fix some mobile-screen issues, rewrite old code, improve loading performance.</li>
                         </ul>
                     </div>

                 </div>
        </div>
    )
}

export default Leadely;
